import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import Stepper from './Stepper';
import BottomStepper from './BottomStepper';
import { Link } from 'react-router-dom';

// 
import PaymentStepper1 from './PaymentStepper1';
import PaymentScreen2  from './PaymentScreen2';
import PaymentScreen3 from './PaymentScreen3';
import PaymentForm from './PaymentForm';

function Payments() {
  const [currentStep, setCurrentStep] = useState(1);
  const history = useHistory();
  const [plan , setPlan] = useState('');

  const handleChange = (e) => {
      console.log(e);
      setPlan(e);
  };

  const submitForm = async (e)  => {
    e.preventDefault();
    const getUserInfo = localStorage.getItem('userInfo');
    const userInfo = JSON.parse(getUserInfo);
    console.log('yes' , userInfo);
    console.log('form' , plan);
    const config = {
        headers: {
          'Content-Type': 'application/json',
        }
      };
    // const payLoad =     {
    //     "method": "create",
    //     "store": 26107,
    //     "authkey": "WjvHt@5rVkM~trh6",
    //     "language": "en",
    //     "order": {
    //       "cartid": "213213",
    //       "test": "1",
    //       "amount": "100",
    //       "currency": "AED",
    //       "description": "Test Transaction",
    //       "trantype": "Auth"
    //     },
    //     "customer": {
    //     "ref": 2021,
    //       "email": "test1@test.com",
    //       "name": {
    //         "forenames": "John",
    //         "surname": "Smith"
    //       },
    //       "address": {
    //         "line1": "5 th street",
    //         "city": "Dubai",
    //         "country": "AE"
    //       },
    //       "phone": "0123456789"
    //     },
    //     "return": {
    //       "authorised": "http://www.example.com/",
    //       "declined": "http://www.example.com/",
    //       "cancelled": "http://www.google.com/"
    //     }
      
    //   }

    const payLoad = {
        email : userInfo['email'],
        phone : userInfo['phone'],
        whatsapp : userInfo['whatsapp'],
        firstname : userInfo['firstname'],
        lastname : userInfo['lastname'],
        planPrice : plan,
    }
    const telr = 'payments/';
    try {
        const res = await axios.post(telr , payLoad , config);
        if (res?.data['order']['url']) {
            // console.log(res?.data['order']['url']);
            localStorage.removeItem('userInfo');
            window.location.assign(res?.data['order']['url']);
        }
      } catch (error) {
        console.log(error);
      }
  };
    
    const getForm = () => {
        switch (currentStep) {
          case 1:
            return (
              <PaymentStepper1
                updateStep={setCurrentStep}
                handleChange={handleChange}
              />
            );
          case 2:
            return <PaymentScreen2 updateStep={setCurrentStep} />;
          case 3:
            return <PaymentScreen3 submitForm={submitForm}  updateStep={setCurrentStep} />;
          case 4:
            return <PaymentForm submitForm={submitForm} updateStep={setCurrentStep} />;
          default:
            return null;
        }
      };
return(
  <>
   {getForm()}
  </>
)
}

export default Payments;